.footer{display:grid;place-items:center;height:50px}.footer a,.footer a:hover,.footer a:visited,.footer a:focus{color:#fff;text-decoration:underline}

.header{display:flex;align-items:center;height:70px;padding-left:30px;border-bottom:1px solid #26282b}.header h1{position:relative;letter-spacing:.02em;font-weight:400;border-radius:20px;display:grid}.header h1::after{content:'';position:absolute;bottom:1px;left:0;width:100%;height:1px;background-image:linear-gradient(to right, #dd1141, #2666c7 70%)}@media (max-width: 670px){.header h1{font-size:30px}}@media (max-width: 500px){.header h1{font-size:20px}}.header__container{display:flex;justify-content:space-between;align-items:center;width:95vw;margin:0 auto}.nav{display:flex;justify-content:space-between}.nav a,.nav a:visited,.nav a:active{color:#fff;text-decoration:none;text-transform:lowercase;font-weight:300;font-size:20px;margin-right:40px;letter-spacing:.02em}@media (max-width: 500px){.nav a,.nav a:visited,.nav a:active{margin-right:10px}}

.preloader{color:#fff;-webkit-animation:rotater 1s infinite;animation:rotater 1s infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;padding:0;margin:0;font-size:70px;transform-origin:center;line-height:1;width:70px;height:70px}.preloader-wrapper{padding:100px}@-webkit-keyframes rotater{from{transform:rotate(0)}to{transform:rotate(360deg)}}@keyframes rotater{from{transform:rotate(0)}to{transform:rotate(360deg)}}

.select{position:relative;height:40px;background:transparent;width:80px}.select:after{content:'';position:absolute;top:0;width:0;height:0;right:10px;bottom:0;margin:auto;border-style:solid;border-width:5px 5px 0 5px;border-color:#0361cc transparent transparent transparent;pointer-events:none}.select:before{width:25px;position:absolute;top:1px;right:1px;bottom:1px;background:whitesmoke;border-top-right-radius:20px;border-bottom-right-radius:20px;border-left:1px solid #ccc;content:'';pointer-events:none}.select:hover:before{background:#cfedff}.select select{font-size:18px;border:none;box-shadow:none;border-radius:0;background:#fff;height:100%;width:100%;cursor:pointer;outline:none;padding-right:35px;padding-left:15px;border-radius:20px;-moz-appearance:none;-webkit-appearance:none}.select select:-moz-focusring{color:transparent;text-shadow:0 0 0 #000}.select select::-ms-expand{display:none}.select select:focus{border-color:#63ffb4}@media all and (min-width: 0\0) and (min-resolution: 0.001dpcm){.select select{padding-right:0}.select:after,.select:before{display:none}}

.comparison{display:grid;place-items:center;width:100%;font-size:50px}

